body {
  margin: 0;
  padding: 0;
}

.header {
  height: 80px;
  background: linear-gradient(to right, #ffafbd, #ffc3a0);
  justify-content: center;
  align-items: center;
  display: flex;
}

.header h1 {
  color: #fff;
  text-transform: uppercase;
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 36px;
  font-weight: bold;
}

.header-container {
  width: 90%;
  max-width: 1200px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.search-form {
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 20px;
  align-items: center;
  padding: 10px 20px;
  display: flex;
}

.search-form input[type="text"] {
  width: 100%;
  color: #292929;
  border: none;
  margin-right: 10px;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  font-weight: bold;
}

.search-form input[type="text"] ::-ms-input-placeholder {
  color: #292929;
}

.search-form input[type="text"] ::placeholder {
  color: #292929;
}

.search-form input[type="text"]:focus {
  outline: none;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #ffafbd;
  background-image: linear-gradient(to right, #ffafbd, #ffc3a0);
  border: none;
  border-radius: 20px;
  padding: 10px 20px;
  font-family: Montserrat, sans-serif;
  font-size: 18px;
  font-weight: bold;
  transition: all .2s ease-in-out;
}

.search-form button[type="submit"]:hover {
  transform: scale(1.1);
}

.example {
  color: #2f2f37;
  max-width: 960px;
